@import "../../../Root/Colors.css";
.circleDiv {
  display: grid;
  place-items: center;
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin: 30px 15px;
  animation: slideInUp 200ms ease-out;
  box-shadow: 0px 24px 38px 3px rgba(0, 0, 0, 0.14),
    0px 9px 46px 8px rgba(0, 0, 0, 0.12), 0px 11px 15px -7px rgba(0, 0, 0, 0.2);
  transition: transform 200ms ease-out;
}
.circleDiv a {
  outline: none;
}

@media screen and (min-width: 575px) {
  .circleDiv:hover,
  .circleImg:focus {
    outline: none;
    transform: scale(1.2);
  }
  .circleDiv:hover h1,
  .circleDiv:focus h1 {
    outline: none;
    transform: translateY(-100%) scale(0.5);
  }
  .circleDiv h1 {
    pointer-events: none;
  }
  .circleDiv:hover .circleImg,
  .circleDiv:focus .circleImg {
    outline: none;
    filter: grayscale(0);
  }
}
@keyframes slideInUp {
  from {
    transform: translateX(-20px);
  }
  to {
    transform: translateX(0);
  }
}
@media screen and (max-width: 574px) {
  .circleDiv {
    margin: 25px;
  }
  .circleDiv:hover,
  .circleDiv:active {
    transform: scale(1.2) translateX(10px);
  }
  .circleDiv:hover h1,
  .circleDiv:active h1 {
    font-size: 24px;
    transform: scale(0.65) translateX(-16px);
    color: var(--hover-clr);
    border-color: var(--hover-clr);
  }
  .circleDiv:hover .circleImg,
  .circleDiv:active .circleImg {
    filter: grayscale(0);
  }
}
.circleImg {
  pointer-events: none;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  filter: grayscale(100%);
  transition: filter 400ms ease-out;
}
