.wrapper {
  display: flex;
  flex-direction: column;
  letter-spacing: 1px;
  position: absolute;
  top: 25px;
  right: 25px;
  text-align: right;
}
@media screen and (max-width: 574px) {
  .wrapper {
    top: 25px;
    right: 25px;
    text-align: right;
    line-height: 24px;
  }
}
