@import "./Colors.css";
html {
  background: url(../Components/Contents/assets/linedpaper.png);
}
.canvas {
  width: 100vw;
  min-height: 100vh;
  position: relative;
  top: 0;
  left: 0;
}
body::-webkit-scrollbar {
  width: 6px;
}

body::-webkit-scrollbar-track {
  background: transparent;
}

body::-webkit-scrollbar-thumb {
  background-color: var(--txt-clr);
  border-radius: 3px;
}
body {
  overflow-x: hidden;
}
